<template>
    <article v-html="htmlData"></article>
</template>

<script>
import axios from 'axios';

export default {
    data: function() {
        return {
            htmlData: ""
        };
    },
    created: function() {
        this.fetchHTML();
    },
    watch: {
        '$route': 'fetchHTML'
    },
    methods: {
        fetchHTML() {
            let that = this;
            axios.get('/' + this.$route.params.name + '.html').then(function(response) {
                that.htmlData = response.data;
            });
        }
    }
}
</script>

<style lang="scss">
    article {
        text-align: left;
        padding: 0 64px;
    }

    section {
        margin-bottom: 32px;
    }

    h1, h2, h3, h4, h5, h6 {
        margin: 20px 0 0 0
    }

    h1 {
        font-size: 32px
    }

    h2 {
