<template>
    <aside>
        <img src="../assets/khu_logo.png" height="90" />
        <div id="title_name">HACKER</div>

        <index-list :items="indexItems" />
    </aside>
</template>

<script>
    import IndexList from "./IndexList.vue";
    import axios from "axios";

    export default {
        name: 'SideBar',
        components: {
            IndexList
        },
        data: function() {
            return {
                indexItems: []
            }
        },
        created: function() {
            let that = this;
            axios.get('/index.json').then(function(response) {
                that.indexItems = response.data;
            });
        }
    }
</script>

<style lang="scss" scoped>

    aside {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        width: 300px;
        box-sizing: border-box;
        border-right: 1px solid #cccccc;
    }

    #title_name {
        font-size: 36px;
        font-weight: bold
    }

</style>

